import React, { useEffect, useState } from 'react';
// 滚动加载组件
import InfiniteScroll from 'react-infinite-scroller';
import { LoadMoreButton } from './load_more_button_conpoment';

// react-infinite-scroller install: npm install react-infinite-scroller --save

import ListsItem from './list_item';

const ProductsList = ({ productsData, loadMore, loading, hasMore, ItemRendererComponent, rowHeight }) => {
  const [screenWidth, setScreenWidth] = useState(500);
  useEffect(() => {
    console.log('set document');
    if (typeof document !== `undefined`) {
      setScreenWidth(document.body.clientWidth);
    }
  }, []);

  const ListComponent = () => {
    // 列表组件
    return (
      <div className="hero min-h-screen bg-base-200">
        <div className="hero-content text-center">
        <div className="max-w-md">
          {productsData.map((currentData, index) => {
              return (
                <div className="card w-96 bg-base-100 shadow-xl mb-4" key={index.toString()}>
                  <div className="card-body">
                    <ItemRendererComponent item={currentData} index={index}></ItemRendererComponent>
                  </div>
                </div>
              )
            }
          )}
      </div>
      </div>
      </div>
    )
  };

  return (
    <div style={{ overflow: 'auto' }}>
      <InfiniteScroll
        initialLoad={false}
        pageStart={0}
        loadMore={loadMore}
        hasMore={!loading && hasMore}
        useWindow={true}
      >
        <ListComponent></ListComponent>
        <LoadMoreButton hasMore={hasMore} onClick={loadMore} loading={loading}></LoadMoreButton>
        <div style={{ height: '50px', width: '100%', margin: '30px 0px' }}></div>
      </InfiniteScroll>
    </div>
  )
}

ProductsList.defaultProps = {
  // productsData item={image_cos_name1: '', len_title: '', effects, '', len_price: '', len_sale_price: ''}
  productsData: [],
  loadMore: (startIndex, stopIndex) => {
    // 读取更多数据
    console.log('Start-index:', startIndex, '; stopIndex:', stopIndex);
  },
  loading: false,
  hasMore: false,
  ItemRendererComponent: ({ item, index }) => {
    // 必须 每一行渲染 的组件
    // console.log(item);
    return (<div>
      <ListsItem
        listsIndex={index}
        url={item.image_cos_name1}
        productTitle={item.len_title}
        productEffects={item.effects}
        sourcePrice={item.len_price}
        salePrice={item.len_sale_price}
        clickDetailHandle={(listsIndex) => { console.log(listsIndex) }}
      ></ListsItem>
    </div>);
  },
  rowHeight: 240
}

export default ProductsList;
